import { RequestOptions } from './request-options.interface';

export interface HttpServiceInterface {
  get<T>(
    url: string,
    options?: RequestOptions,
    queryParams?: Record<string, string | number | boolean>,
  ): Promise<T>;

  post<T>(
    url: string,
    body: string | object,
    options?: RequestOptions,
  ): Promise<T>;

  put<T>(
    url: string,
    body: string | object,
    options?: RequestOptions,
  ): Promise<T>;
}
